import React from "react";

const shuffle = (array) => {
    let m = array.length, t, i;
    while (m) {
  
      i = Math.floor(Math.random() * m--);
  
      t = array[m];
      array[m] = array[i];
      array[i] = t;
    }
  
    return array;
  }

  export default shuffle;

  export const correctPhoto = (e,preview) =>{
    const objectUrl = URL.createObjectURL(e.target.files[0])
    preview(objectUrl)
  }